const TableReact = () => {
    let values = products.map
    let borderForBodies = "border-0 sm:border-b sm:border-card md:border-b md:border-card lg:border-b lg:border-card xl:border-b xl:border-card 2xl:border-b 2xl:border-card"
    let lastBody = "border-b border-card sm:border-0 md:border-0 lg:border-0 xl:border-0 2xl:border-0"
    let thBg = "bg-none sm:bg-card md:bg-card lg:bg-card xl:bg-card 2xl:bg-card"
    return (
        <table
            className="sm:inline-table w-full flex flex-row bg-background  overflow-hidden text-onBackground rounded-t-lg">
            <thead>
            {products?.map((data, index) => (
                <tr
                    className={` sm:bg-transparent flex flex-col sm:table-row  sm:mb-0 ${
                        index === 0 ? "sm:flex" : "sm:hidden"
                    } ${thBg}`}
                    key={index}
                >
                    <th className="py-3 px-4 text-left sm:rounded-none">
                        Name
                    </th>
                    <th className="py-3 px-4 text-left">
                        Absence Hours
                    </th>
                    {data.exams.map((exam, index) => (
                        <th className={`py-3 px-4 text-left ${index === data.exams.length -1 ? lastBody : ""}`}>{exam.name}
                        <span className="text-xs text-onBackgroundCaption"> ({exam.percentage}%)</span>
                        </th>
                    ))}

                </tr>
            ))}
            </thead>
            <tbody className="flex-1 sm:flex-none">
            {products?.map((data, index) => (
                <tr
                    className="flex flex-col sm:table-row  sm:mb-0"
                    key={index}
                >
                    <td className={`${borderForBodies} py-3 px-4`}>
                        {data?.name}
                    </td>
                    <td className={`${borderForBodies} py-3 px-4`}>
                        {data?.absence_hours}
                    </td>
                    {data.exams.map((exam,index)=> (
                        <td className={`${ index === data.exams.length -1? "border-b border-card": borderForBodies} py-3 px-4`}>
                            {exam.value}
                        </td>
                    ))}


                </tr>
            ))}
            </tbody>
        </table>
    );
};